.link-card-container{
    min-width: 320px;
    min-height: 200px;
    width : 300px ; 
    height: 200px ; 
    position: relative;
    border: 1px solid white ; 
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    margin-bottom: 100px;
    margin-top: -70px;
    margin-left: 20px ; 
    margin-right: 20px ;
    border-radius: 10px;
    cursor: pointer;

}

.link-card-container img { 
    position: absolute;
    top: 2%;
    left: 50% ; 
    transform: translate(-50% , -0%);
}

.link-card-container h1 { 
    position: absolute;
    left: 50% ; 
    top: 55%;
    transform: translate(-50% , -0%);
}

.link-card-container h1 { 
    text-align: center ;
    
    font-size: 35px;
    background: -webkit-linear-gradient(0deg, #200957 0%, #434892 55%, #32ce92 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

}